
import { defineComponent } from 'vue';
import { COM207001SC01_RES, COM207001SC01_REQ_Register } from '@/shared/type/COM207001SC01';
import { modalController } from '@ionic/vue';
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';
import ModifyCard from '@/shared/components/ModifyCard/ModifyCard.vue';
import { screenMode } from '@/shared/type/Approval.type';
import { APM20200113_RES } from '@/shared/type/APM20200113.type';

export default defineComponent({
    name: 'com207001sc01',
    components: {
        BreadCrumb,
        ModifyCard
    },

    data() {
        return {
            modifyCard: {
                registerTellerId: '',
                registerTellerName: '',
                registerDate: '',
                registerTime: '',
                updatedTellerId: '',
                updatedTellerName: '',
                updatedDate: '',
                updatedTime: '',
            },
            isInValidInput: false as boolean,
            isEdit: true as boolean,
            isDisabled: true as boolean,
            userRoleDetail: {
                userRoleID: "",
                userRoleName: "",
                userRoleDescription: "",
                userRoleStatusCode: "01"
            } as COM207001SC01_RES,
            operationMode: 'normal' as screenMode,
            approvalRes: {} as any
        }
    },

    computed: {
        approvalType(): string {
            return this.$route.query.approvalType ? this.$route.query.approvalType.toString() : ''
        },

        UserRoleStatusCode() {
            return this.$store.state.CommonCodeModule.commonCode.UserRoleStatusCode
        },

        displayType() {
            return this.$route.query.type;
        },
        btnCancel(): boolean {
            if (this.$route.query.type == 'register' || this.isEdit == false) {
                return true;
            }
            return false;
        },
        btnList(): boolean {
            if (this.$route.query.type == 'edit' && this.isEdit == true) {
                return true;
            }
            return false;
        },
        btnEdit(): boolean {
            if (this.$route.query.type == 'edit' && this.isEdit == true) {
                return true;
            }
            return false;
        },
        btnSave(): boolean {
            if (this.$route.query.type == 'register' || this.isEdit == false) {
                return true;
            }
            return false;
        }
    },

    async created() {
        if (this.approvalType === '') {
            switch (this.$route.query.type) {
                case 'register':
                    this.isEdit = false;
                    this.isDisabled = false;
                    this.userRoleDetail.userRoleStatusCode = this.UserRoleStatusCode ? this.UserRoleStatusCode[0].key : ''
                    break;
                case 'edit':
                    this.requestUserRoleDetail();
                    break;
            }
        } else {
            await this.$approval.getAuthorizationDetail().then((authDetail: APM20200113_RES) => {
                let authDetailParsed = JSON.parse(authDetail.body.requestContent)
                this.approvalRes = authDetail.body
                this.operationMode = this.approvalType == 'my-request' ? 'read' : this.approvalType == 'my-approval' ? 'approval' : 'normal'
                this.userRoleDetail = {
                    userRoleID: authDetailParsed.userRoleID,
                    userRoleName: authDetailParsed.userRoleName,
                    userRoleDescription: authDetailParsed.userRoleDescription,
                    userRoleStatusCode: authDetailParsed.userRoleStatusCode
                }
            })
        }
    },

    methods: {

        async requestUserRoleDetail() {
            const trCode = "COM20700112"
            const isBizMob = false;
            const body = { userRoleID: this.$route.query.userRoleID }
            await this.$request.getRequest(trCode, body, isBizMob)
                .then((res: any) => {
                    this.userRoleDetail = res.body
                    this.modifyCard = {
                        registerTellerId: res?.body.firstRegisterTellerID,
                        registerTellerName: res?.body.firstRegisterTellerName,
                        registerDate: res?.body.firstRegisterDate,
                        registerTime: res?.body.firstRegisterTime,
                        updatedTellerId: res?.body.lastChangeTellerID,
                        updatedTellerName: res?.body.lastChangeTellerName,
                        updatedDate: res?.body.lastChangeDate,
                        updatedTime: res?.body.lastChangeTime
                    }
                })
                .catch((error: Error) => console.log(error))
        },

        async handleRegister() {
            const { userRoleID, userRoleName, userRoleDescription, userRoleStatusCode } = this.userRoleDetail;
            const body: COM207001SC01_REQ_Register = {
                userRoleID: userRoleID,
                userRoleName: userRoleName,
                userRoleDescription: userRoleDescription,
                userRoleStatusCode: userRoleStatusCode
            };
            const res: any = await this.$request.getRequest("COM20700121", body, false)
            if (res.header.result) {
                this.$router.push('COM207001SC00');
                this.$dialog.onOpen({
                    content: this.$t("message.register_success"),
                    dialogMode: 'Success',
                    callback: () => {
                        modalController.dismiss()
                    }
                });
            }
        },

        async handleEdit() {
            const { userRoleID, userRoleName, userRoleDescription, userRoleStatusCode } = this.userRoleDetail;
            const body: COM207001SC01_RES = {
                userRoleID: userRoleID,
                userRoleName: userRoleName,
                userRoleDescription: userRoleDescription,
                userRoleStatusCode: userRoleStatusCode
            }
            const res: any = await this.$request.getRequest("COM20700131", body, false);
            if (res.header.result) {
                this.isDisabled = true;
                this.isEdit = true;
                this.$dialog.onOpen({
                    content: this.$t("message.update_success"),
                    dialogMode: 'Success',
                    callback: () => {
                        this.requestUserRoleDetail()
                        modalController.dismiss()
                    }
                });
            } else {
                console.log(res.header);
            }
        },

        handleSave() {
            switch (this.$route.query.type) {
                case 'register':
                    this.handleRegister();
                    break;
                case 'edit':
                    this.handleEdit();
                    break;
            }
        },

        checkSpace(event: Event) {
            this.$validate.validateSpace(event)
        },

        handleClickAction(actionType: string) {
            switch (actionType) {
                case 'cancel':
                    this.$dialog.onOpen({
                        content: this.$route.query.type === 'edit' ? this.$t('message.cancel_without_saving_changes') : this.$t('message.are_you_sure_you_want_to_cancel_registration?'),
                        dialogMode: "Question",
                        callback: () => {
                            if (this.$route.query.type == 'register') {
                                this.$router.push('COM207001SC00');
                            } else {
                                this.isEdit = true;
                                this.isDisabled = true;
                                this.requestUserRoleDetail()
                            }
                        }
                    })
                    break;
                case 'list':
                    this.$router.push('COM207001SC00');
                    break;
                case 'edit':
                    this.isDisabled = false;
                    this.isEdit = false;
                    break;
                case 'save':
                    var { userRoleDescription, userRoleID, userRoleName, userRoleStatusCode } = this.userRoleDetail;
                    if (!userRoleID || !userRoleName || !userRoleDescription || !userRoleStatusCode) {
                        this.isInValidInput = true;
                    } else {
                        this.$dialog.onOpen({
                            content: `${this.$t("message.would_you_like_to_save")}`,
                            dialogMode: "Question",
                            callback: () => {
                                this.handleSave();
                            }
                        })
                    }
                    break;
            }
        },
    }
})

